.messages_list {
    color: whitesmoke;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-end;
}

.messages_list li {
    list-style: none;
    background-color: rgba(113, 121, 147, 0.4);
    padding: 10px;
    border-radius: 7px;
    margin-right: 20px;
    display: inline-block;
}

.messages_list li.my {
    align-self: flex-end;
    background-color: rgba(69, 78, 109, 0.4);
}

.messages_list li + li {
    margin-top: 10px;
}