.window {
    background-color: rgba(16, 18, 27, .3);
    display: flex;
    flex-direction: column;
    border-radius: 14px;
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
}

.window__container {
    width: 100%;
    padding: 5px 10px;
}

.window__header {
    width: 100%;
    min-height: 60px;
    display: flex;
    align-items: center;
}

.window__controls {
    width: 15px;
    height: 15px;
    background-color: #f96057;
    border-radius: 50%;
    box-shadow: 24px 0 0 0 #f8ce52, 48px 0 0 0 #5fcf65;
    margin-right: 195px;
    flex-shrink: 0;
    margin-left: 10px;
}

.window__inner {
    flex-grow: 1;
    display: flex;
}

.window__sidebar {

}

.window__content {
    background-color: rgba(16, 18, 27, .4);
    flex-grow: 1;
    height: 100%;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
}


